import { forwardRef } from 'react';

import {
  Wrapper,
  ImageWrapper,
  Text,
  Sponsored,
} from './RelatedArticlesCarouselItem.styles';
import { RelatedArticlesCarouselItemProps } from './RelatedArticlesCarouselItem.types';

const RelatedArticlesCarouselItem = forwardRef<
  HTMLAnchorElement,
  RelatedArticlesCarouselItemProps
>(function RelatedArticlesCarouselItem(
  { href, title, image, isSponsored = false, ...rest },
  ref,
) {
  return (
    <Wrapper {...rest} ref={ref} href={href}>
      <ImageWrapper>{image}</ImageWrapper>
      <Text>
        {isSponsored && <Sponsored>sponsored</Sponsored>}
        {title}
      </Text>
    </Wrapper>
  );
});

export default RelatedArticlesCarouselItem;
