import IconButton from '#components/IconButton';
import RelatedArticlesCarouselItem from '#components/RelatedArticlesCarouselItem';
import Tag from '#components/Tag';

import {
  Wrapper,
  StyledCarousel as Carousel,
  Title,
} from './RelatedArticlesCarousel.styles';
import propTypes, { buttonPropTypes } from './RelatedArticlesCarousel.types';

import ChevronLeftSVG from '#icons/chevron-left-solid-32.svg';
import ChevronRightSVG from '#icons/chevron-right-solid-32.svg';

const ButtonPrev = ({ disabled, onClick, ...props }) => (
  <IconButton
    {...props}
    size="large"
    svg={ChevronLeftSVG}
    isDisabled={disabled}
    onClick={onClick}
  />
);
ButtonPrev.propTypes = buttonPropTypes;

const ButtonNext = ({ disabled, onClick, ...props }) => (
  <IconButton
    {...props}
    size="large"
    svg={ChevronRightSVG}
    isDisabled={disabled}
    onClick={onClick}
  />
);
ButtonNext.propTypes = buttonPropTypes;

const navigateCarousel = (items, index) =>
  items[index].scrollIntoView({
    behavior: 'smooth',
    block: 'nearest',
    inline: 'start',
  });

const RelatedArticlesCarousel = ({
  className,
  items,
  title,
  renderSlide,
  ...rest
}) => (
  <Wrapper className={className}>
    <Title>
      <Tag isExpanded={true}>{title}</Tag>
    </Title>
    <Carousel
      {...rest}
      onNavigate={navigateCarousel}
      ButtonPrev={ButtonPrev}
      ButtonNext={ButtonNext}
      items={items}
      renderSlide={
        renderSlide ||
        (({ itemIndex, ref, item }) => (
          <RelatedArticlesCarouselItem {...item} key={itemIndex} ref={ref} />
        ))
      }
    />
  </Wrapper>
);
RelatedArticlesCarousel.propTypes = propTypes;

export default RelatedArticlesCarousel;
