import { styled } from 'styled-components';

import Carousel, { Slider, Group, Nav } from '#components/Carousel';
import FullBleed, {
  BREAKPOINT as FULL_BLEED_BREAKPOINT,
} from '#components/FullBleed';

export const StyledCarousel = styled(Carousel)`
  ${Slider} {
    gap: ${({ theme }) => theme.spacing.x2}px;
  }

  ${Nav} {
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    width: var(--nav-width);
    height: var(--button-width);
  }

  @media ${FULL_BLEED_BREAKPOINT} {
    margin-inline: calc(var(--bleed) * -1);

    ${Group} {
      scroll-margin: 0 var(--bleed);

      &:first-child {
        padding-left: var(--bleed);
      }

      &:last-child {
        padding-right: var(--bleed);
      }
    }

    ${Nav} {
      right: var(--bleed);
    }
  }
`;

export const Wrapper = styled(FullBleed)`
  --bleed: ${({ theme }) => theme.dimension.bleed}px;
  --button-width: ${({ theme }) => theme.dimension.minTouchArea}px;
  --nav-width: calc(var(--button-width) * 2);

  display: flex;
  position: relative;
  flex-direction: column;
  row-gap: ${({ theme }) => theme.spacing.x1_5}px;
  padding: ${({ theme }) => theme.spacing.x1_5}px 0
    ${({ theme }) => theme.spacing.x2}px;
  border: solid ${({ theme }) => theme.color.divider.light};
  border-width: 1px 0;

  @media ${FULL_BLEED_BREAKPOINT} {
    padding-right: var(--bleed);
    padding-left: var(--bleed);
  }
`;

export const Title = styled.div`
  display: block;
  padding-right: calc(
    var(--nav-width) + ${({ theme }) => theme.spacing.x1_5}px
  );
`;
